@import 'global.css';

.frame
{
    width:100%;
    height:100%;
    padding:1%;
    margin:auto;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction:row;
        flex-direction:row;
    -ms-flex-wrap:nowrap;
        flex-wrap:nowrap;
    -ms-flex-pack:center;
        justify-content:center;
    -ms-flex-align:start;
        align-items:flex-start;
    overflow-y:auto;
}

.frame---view-panel
{
    padding:0.3rem;
    max-width:17rem;
    overflow-x:hidden;
    word-wrap:break-word;
    /* height:100%; */
}

.frame---tool-panel
{
    display:block;
    position:-webkit-sticky;
    position:sticky;
    top:0;
}

.frame---navigation-button
{
    display:table;
    font-size:0.81rem;
    padding:0.03rem;
}


